* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #1fb2c2;
  background-color: #0a272a;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

label {
  color: #7633e4;
}

input {
  color: #1f003f;
  background-color: #366368;
  border: 1px solid #1f003f;
  padding: 3px 2px;
}

button {
  color: #cbecf0;
  background-color: #482794;
  border: 1px solid #1d005f;
  padding: 3px;
}

button:hover {
  background-color: #3b0099;
  border-color: #2c686e;
}

button:active {
  background-color: #260064;
}

button[disabled] {
  color: #937a9b;
  background-color: #674979;
  border: 1px dotted #6d6d6d;
}

